.ToolBar{
    height:56px;
    width:100%;
    position: fixed;
    top:0;
    left:0;
    background-color: #703B09;
    display:flex;
    justify-content: space-between;
    align-self: center;
    padding: 0 20px;
    box-sizing: border-box;
    z-index: 90;
}
.ToolBar nav{
    height:100%;
}
@media(max-width:499px){
    .DesktopOnly{
        display:none;
    }
}