.SideDrawer{
    position:fixed;
    color:white;
    width:280px;
    max-width:70%;
    height: 100%;
    left:0;
    top:0;
    z-index:200;
    background-color:white;
    padding:32px 16px;
    box-sizing: border-box;
    transition: transform 0.3s ease-out;
}

@media(min-width:500px){
    .SideDrawer{
        display:none;
    }
}
.Open{
transform:translate(0);
}
.Close{
transform: translate(-100%);
}
